<template>
  <v-ons-page>
    <v-ons-toolbar class="home-toolbar">
      <div class="center">{{ msg }}</div>
    </v-ons-toolbar>

    <div class="header" style="margin: 5% 0;">
      <img src="../assets/Vso59x2r_400x400.jpg" alt="logo">
    </div>

    <div style="text-align: center;"><button @click="init()"><p style="display: inline-block;margin:2% 0;">加速度センサ</p></button></div>
    <div id="force" style="font-size: 64px; text-align: center; margin: 5% 0;">{{ force }}</div>
  </v-ons-page>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome',
      force: ''
    }
  },
  methods: {
    init () {
      window.addEventListener('devicemotion', this.devicemotionHandler)
    },
    devicemotionHandler (event) {
      // 加速度
      // X軸
      const x = event.acceleration.x
      // Y軸
      const y = event.acceleration.y
      // Z軸
      // const z = event.acceleration.z
      if (x > 3 && y > 3) {
        this.force = '右上'
      } else if (x > 3 && y < 3) {
        this.force = '右下'
      } else if (x < 3 && y > 3) {
        this.force = '左上'
      } else if (x < 3 && y < 3) {
        this.force = '左下'
      } else if (x > 3) {
        this.force = '右'
      } else if (x < -3) {
        this.force = '左'
      } else if (y > 3) {
        this.force = '上'
      } else if (y < -3) {
        this.force = '下'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



